.contact-me {
	background-color: $white150;
	.links-out {
		@include inline-lists;

		li:not(:last-child){
			a {
				&:after {
					// content: '/';
				}
			}
		}

		li a {
			margin: rem(5);
			padding: 0 rem(10);
			display: block;
			line-height: 1;

			&:first-child {
