				padding-left: 0;
			}

			span {
				font-family: $suppl-family;
				font-size: rem(24);
				font-style: italic;
				color: $black300;
				font-weight: 300;
				// text-transform: uppercase;
			}
		}

		// li:not(:last-child) a:after{
